html, body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 16px; /* Dimensiune de bază pentru text */
  line-height: 1.5; /* Îmbunătățește lizibilitatea */
  color: #333; /* Culoare standard pentru text */
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden; /* Ascunde bara de scroll globală */
}

/* Header */
.header {
  background: #333;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

.header h1 {
  font-size: 36px;
  color: #fff;
  margin: 0;
}

/* Main Content */
.main-content {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 50px 20px 50px 20px;
  width: 100%;
  height: calc(100vh - 100px); /* Înălțimea disponibilă pentru conținut */
  box-sizing: border-box;
  overflow-y: auto; /* Permite scroll doar pe conținut */
  margin-left: 270px; /* Lăsăm loc pentru tab-uri pe partea stângă */
}

.content {
  width: 80%;
  padding: 20px;
  background: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  overflow-y: auto; /* Permite scroll pe conținut */
}

/* Tab-uri cu position absolute */
.tabs {
  display: flex;
  flex-direction: column;
  padding: 30px;
  position: absolute; /* Plasăm tab-urile în poziție absolută */
  top: 100px; /* Distanța față de partea de sus a paginii */
  left: 0;
  width: 250px; /* Lățimea tab-urilor */
  box-sizing: border-box;
  max-height: calc(100vh - 100px); /* Înălțimea maximă disponibilă pentru tab-uri */
  overflow-y: auto; /* Permite scroll pe tab-uri */
  z-index: 10; /* Asigură că tab-urile sunt deasupra altor elemente */
}

.tabs button {
  width: 110px;
  height: 110px;
  border: none;
  cursor: pointer;
  margin-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
  transition: transform 0.3s ease;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
  transform: scale(1);
}

.tab-image {
  width: 110%;
  height: 110%;
  object-fit: cover;
  border-radius: 50%;
}

.tabs button:hover {
  transform: scale(1.1);
}

.tabs button.active {
  color: white;
}

.tabs button::after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 14px;
  opacity: 0;
  transition: opacity 1s ease;
  text-shadow: 6px 6px 15px rgba(0, 0, 0, 0.9);
}

.tabs button:hover::after {
  opacity: 1;
}

.tabs button.active::after {
  opacity: 1; /* Asigură-te că textul este vizibil */
  color: white; /* Ajustează culoarea textului dacă este necesar */
  font-weight: bold; /* Asigură că textul se evidențiază */
  text-shadow: 6px 6px 15px rgba(0, 0, 0, 0.9); /* Adaugă un efect de evidențiere */
}

.social-links a {
  display: inline-block;
  margin-right: 15px;
}

.social-links img {
  margin-right: 10px;
  width: 80px;
  height: 80px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.social-links img:hover {
  transform: scale(1.3);
}

/* Footer */
.footer {
  text-align: center;
  padding: 20px 0; /* Modificat pentru a fi la fel ca padding-ul din header */
  background: #333; /* Culoarea fundalului footerului */
  color: #fff;
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 999;
}

/* Styling pentru Contact */
.footer .contact-and-social .contact-list {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
  gap: 30px; /* Distanța între iconițele de contact */
}

.footer .contact-and-social .contact-icon {
  width: 40px; /* Dimensiunea iconiței */
  height: 40px;
  cursor: pointer;
  overflow: hidden;
  transition: transform 0.3s ease;
  
}

.footer .contact-and-social .contact-icon:hover {
  transform: scale(1.3); /* Efect de hover pentru iconițe */
}



/* Media queries pentru dispozitive mici */
@media (max-width: 768px) {
  .tabs {
    width: 60vw;
    padding: 15px;
  }
  .main-content {
    flex-direction: column;
    padding: 20px;
  }
  .content {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
  .header h1 {
    font-size: 24px;
  }

  .footer .contact-section {
    text-align: center;
  }
}

/* Media query pentru tablete */
@media (max-width: 1024px) and (min-width: 769px) {
  .tabs {
    width: 35vw;
    padding: 15px;
  }
  .main-content {
    padding-left: 25%;
  }
  .content {
    width: 75%;
    margin-left: 0;
    margin-right: 0;
  }
}

/* Media query pentru desktopuri mari */
@media (min-width: 1280px) {
  .tabs {
    width: 15vw;
  }
}
